import React, { Suspense } from "react";

import './AboutMe.scss';
import '../../../../Shared/Scss/typography.scss';
import '../../../../Shared/Scss/links.scss';
import detect from "detect.js";
//import ManMobile from "../../../../Shared/images/svg/ManMobile/ManMobile";

const ManMobile = React.lazy( () => import('../../../../Shared/images/svg/ManMobile/ManMobile') );

export default class AboutMe extends React.Component {
    constructor(props) {
        super(props);

        const userAgent = detect.parse(navigator.userAgent);
        this.isNeedToFix = /Windows|Android/i.test(userAgent.os.family);
        this.isFirefox = /Firefox/i.test(userAgent.browser.family);
    }

    render() {
        const contentText = this.props.contentText;

        return (
            <section className="about-me">
                <div className="about-me__container">
                    <h2 className={`about-me__title heading2${this.isNeedToFix || this.isFirefox ? ' overpassFix' : ''}`}>{contentText.title}</h2>
                    <div className="about-me__content">
                        <div className="about-me__content-img">
                            <Suspense fallback=''>
                                <ManMobile />
                            </Suspense>
                        </div>
                        <div className="about-me__content-info">
                            <h3 className={`about-me__content-info-title heading3${this.isNeedToFix || this.isFirefox ? ' overpassFix' : ''}`}>{contentText.subtitle}</h3>
                            <p className="about-me__content-info-text paragraph2">{contentText.infoText1}</p>
                            <br />
                            <p className="about-me__content-info-text paragraph2">{contentText.infoText2}</p>
                            <a className="about-me__content-info-link bold-link" href="/">{contentText.buttonSignUp}</a>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}